body{
  margin: 0;
  padding: 0;
  font-family: 'Courier New', monospace;
  background-color: #282c34;
}

.app {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.app-header {
  color: white;
  text-align: center;
  margin-bottom: 20px;
}

.app-header a {
  color: #61dafb; 
  text-decoration: none;
}

.app-header a:hover {
  text-decoration: underline;
}

.terminal {
  width: 80%;
  max-width: 800px;
  height: 400px;
  background-color: black;
  padding: 20px;
  color: lime;
  font-family: 'Courier New', monospace;
  margin: 0 auto;
  border-radius: 5px;
  box-shadow: 0 0 20px 5px rgba(0,0,0,0.5);
  text-align: left;
}

.input_line{
  display: flex;
  
}
.terminal-input {
  width: 100%;
  text-align: left;
  background: transparent;
  color: inherit;
  border: none;
  outline: none;
}

.history {
  max-height: 300px;
  overflow-y: auto;
}

